<template>
  <div class="mt-16">
    <div v-if="!hasResults">
      <h3 class="text-white text-center font-extrabold text-4xl px-2">
        Нет результатов
      </h3>
      <div>
        <img
          class="w-3/4 h-3/4 md:w-2/4 md:h-2/4 mx-auto pt-8 pb-4"
          src="../assets/undraw_not_found_-60-pq.svg"
          alt=""
        />
      </div>
    </div>
    <!-- Фильмы -->
    <div v-if="result.movies.length" class="px-32">
      <h3
        class="text-white font-extrabold text-4xl text-center hover:text-blue-500"
      >
        Фильмы
      </h3>
      <div class="grid md:grid-cols-5 gap-4 place-items-center">
        <span
          v-for="movie in result.movies"
          :key="movie.id"
          class="transition hover:scale-110"
        >
          <router-link :to="'/' + movie.url" class="text-white font-bold">
            <img
              class="rounded-xl mt-8 mx-auto w-full h-full md:w-2/4 md:h-2/4 hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
              :src="movie.post"
            />
            <h4 class="text-center px-2 mb-8">{{ movie.title }}</h4>
          </router-link>
        </span>
      </div>
    </div>

    <!-- Фильмы по жанрам -->
    <div v-if="result.genres.length" class="px-32">
      <h3
        class="text-white font-extrabold text-4xl text-center hover:text-blue-500"
      >
        Фильмы по жанрам
      </h3>
      <div class="grid md:grid-cols-5 gap-4 place-items-center">
        <span
          v-for="movie in result.genres"
          :key="movie.id"
          class="transition hover:scale-110"
        >
          <router-link :to="'/' + movie.url" class="text-white font-bold">
            <img
              class="rounded-xl mt-8 mx-auto w-full h-full md:w-2/4 md:h-2/4 hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
              :src="movie.post"
            />
            <h4 class="text-center px-2 mb-8">{{ movie.title }}</h4>
          </router-link>
        </span>
      </div>
    </div>

    <!-- Актёры -->
    <div v-if="result.actors.length" class="px-32">
      <h3
        class="text-white font-extrabold text-4xl text-center hover:text-blue-500"
      >
        Актёры
      </h3>
      <div class="grid md:grid-cols-5 gap-2 place-items-center">
        <span
          v-for="actor in result.actors"
          :key="actor.id"
          class="transition hover:scale-110"
        >
          <router-link :to="'/actors/' + actor.id" class="">
            <img
              class="rounded-xl mt-8 w-full h-full md:w-2/4 md:h-2/4 mx-auto hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
              :src="actor.image"
            />
            <h4 class="text-white text-center md:mb-8 pt-2 hover:text-blue-500">
              {{ actor.name }}
            </h4>
          </router-link>
        </span>
      </div>
    </div>

    <!-- Режиссёры -->
    <div v-if="result.directors.length" class="px-32">
      <h3
        class="text-white font-extrabold text-4xl text-center hover:text-blue-500"
      >
        Режиссёры
      </h3>
      <div class="grid md:grid-cols-5 gap-4 place-items-center">
        <span
          v-for="director in result.directors"
          :key="director.id"
          class="transition hover:scale-110"
        >
          <router-link
            :to="'/directors/' + director.id"
            class="hover:text-cyan-50"
          >
            <img
              class="rounded-xl mt-8 px-2 mx-auto w-full h-full md:w-2/4 md:h-2/4 hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
              :src="director.image"
            />
            <h4 class="text-white text-center md:mb-8 pt-2 hover:text-blue-500">
              {{ director.name }}
            </h4>
          </router-link>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation, Pagination, Scrollbar, A11y } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import { API_URL } from "@/config";

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    const onSwiper = (swiper) => {
      console.log(swiper);
    };
    const onSlideChange = () => {
      console.log("slide change");
    };
    return {
      onSwiper,
      onSlideChange,
      modules: [Navigation, Pagination, Scrollbar, A11y, Grid, Zoom],
    };
  },
  computed: {
    hasResults() {
      const keys = Object.keys(this.result);
      for (let i = 0; i < keys.length; i++) {
        const key = keys[i];
        if (this.result[key].length != 0) {
          return true;
        }
      }
      return false;
    },
  },
};
</script>
<script setup>
import axios from "axios";
import { ref, watchEffect } from "vue";
import { useRoute, useRouter } from "vue-router";

const router = useRouter();
const route = useRoute();

const result = ref({ movies: [], actors: [] });

watchEffect(function () {
  axios.get(`${API_URL}/v1/search/` + route.query.text).then((response) => {
    result.value = response.data;
    console.log(result.value);
  });
});
</script>
