<template>
  <div class="Actor">
    <div
      class="flex flex-wrap md:flex-wrap justify-evenly max-w-screen-md mx-auto mt-32"
    >
      <img :src="actor.image" class="rounded-xl" alt="" />
      <ul class="md:w-2/5 px-14 md:px-0 flex flex-col mt-16">
        <h3 class="font-extrabold text-4xl text-white text-center">
          {{ actor.name }}
        </h3>
        <li class="pt-8">
          <h3 class="font-semibold text-2xl text-white text-left">О персоне</h3>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Дата рождения:</b>
            {{ actor.date_of_birth_str }} • {{ actor.zodiac }}
          </span>
        </li>
        <li>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Место рождения:</b>
            {{ actor.place_of_birth }}</span
          >
        </li>
        <li>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Возраст:</b> {{ actor.age }}
            {{ actor.age_text }}</span
          >
        </li>
      </ul>
    </div>
    <div class="flex flex-wrap md:flex-wrap justify-evenly mt-8 px-8 md:px-12">
      <h3
        class="font-extrabold text-4xl p-8 text-white text-center sm:text-left"
      >
        Биография
      </h3>
      <span class="text-white text-xl pb-12"> {{ actor.description }}</span>
    </div>
    <div
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <h3 class="text-white font-extrabold text-4xl text-left p-8">
        Актёр в фильмах
      </h3>
    </div>
    <swiper
      :modules="modules"
      :slides-per-view="4"
      navigation
      :pagination="{ clickable: true }"
      @slideChange="onSlideChange"
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <swiper-slide v-for="movie in actor.film_actor" :key="movie.id">
        <router-link :to="'/' + movie.url" class="hover:text-cyan-50">
          <img class="rounded-xl mt-6 px-2" :src="movie.post" />
          <h4
            class="text-white text-center px-2 md:mb-8 pt-2 hover:text-cyan-500"
          >
            {{ movie.title }}
          </h4>
        </router-link>
      </swiper-slide>
    </swiper>
    <div
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <h3 class="text-white font-extrabold text-4xl text-left p-8">
        Режиссёр в фильмах
      </h3>
    </div>
    <swiper
      :modules="modules"
      :slides-per-view="4"
      navigation
      :pagination="{ clickable: true }"
      @slideChange="onSlideChange"
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <swiper-slide v-for="movie in actor.film_director" :key="movie.id">
        <router-link :to="'/' + movie.url" class="hover:text-cyan-50">
          <img class="rounded-xl mt-6 px-2" :src="movie.post" />
          <h4
            class="text-white text-center px-2 md:mb-8 pt-2 hover:text-cyan-500"
          >
            {{ movie.title }}
          </h4>
        </router-link>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import { API_URL } from "@/config";
import axios from "axios";
import { getZodiac, getAge, declOfNum, parseDate, formatDate } from "../utils";
export default {
  name: "ActorDetail",
  props: ["id"],
  data() {
    return {
      actor: {
        id: 0,
        category: "фильмы",
        directors: [],
        actors: [],
        genres: [""],
        title: "",
        tagline: "",
        description: "",
        post: "",
        year: 0,
        coutry: "",
        world_premiere: "",
        budget: 0,
        fees_in_usa: 0,
        fees_in_world: 0,
        url: "",
        movie_trailer: "",
      },
    };
  },
  created() {
    this.loadActor();
  },
  methods: {
    async loadActor() {
      const actor = await fetch(
        `${API_URL}/v1/actors/${this.$route.params.id}`
      ).then((response) => response.json());
      actor.date_of_birth = parseDate(actor.date_of_birth);
      actor.date_of_birth_str = formatDate(actor.date_of_birth);
      actor.zodiac = getZodiac(actor.date_of_birth);
      actor.age = getAge(actor.date_of_birth);
      actor.age_text = declOfNum(actor.age, ["год", "года", "лет"]);
      this.actor = actor;
      console.log(this.actor);
      console.log(this.id);
      console.log(actor.date_of_birth);
      document.title = actor.name;
    },
  },
  computed: {
    moviePoster() {
      return this.actor.id == 0
        ? ""
        : "http://127.0.0.1:8000" + this.actor.image;
    },
  },
};
</script>
