<template>
  <div v-if="director" class="Director">
    <div
      class="flex flex-wrap md:flex-wrap justify-evenly max-w-screen-md mx-auto mt-32"
    >
      <img :src="director.image" class="rounded-xl" alt="" />
      <ul class="md:w-2/5 px-14 md:px-0 flex flex-col mt-16">
        <h3 class="font-extrabold text-4xl text-white text-center">
          {{ director.name }}
        </h3>
        <li class="pt-8">
          <h3 class="font-semibold text-2xl text-white text-left">О персоне</h3>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Дата рождения:</b>
            {{ director.date_of_birth_str }} • {{ director.zodiac }}
          </span>
        </li>
        <li>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Место рождения:</b>
            {{ director.place_of_birth }}</span
          >
        </li>
        <li>
          <span class="text-white"
            ><b class="font-bold text-neutral-500">Возраст:</b>
            {{ director.age }} {{ director.age_text }}</span
          >
        </li>
      </ul>
    </div>
    <div class="flex flex-wrap md:flex-wrap justify-evenly mt-8 px-8 md:px-12">
      <h3
        class="font-extrabold text-4xl p-8 text-white text-center sm:text-left"
      >
        Биография
      </h3>
      <span class="text-white text-xl pb-12"> {{ director.description }}</span>
    </div>
    <div
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <h3 class="text-white font-extrabold text-4xl text-left p-8">
        Актёр в фильмах
      </h3>
    </div>
    <swiper
      :modules="modules"
      :slides-per-view="4"
      navigation
      :pagination="{ clickable: true }"
      @slideChange="onSlideChange"
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <swiper-slide
        v-for="movie in director.movie"
        :key="movie.id"
        class="transition hover:scale-110"
      >
        <router-link
          :to="'/' + movie.url"
          class="hover:text-cyan-50 transition hover:scale-110"
        >
          <img class="rounded-xl mt-6 px-2" :src="movie.post" />
          <h4
            class="text-white text-center px-2 md:mb-8 pt-2 hover:text-cyan-500"
          >
            {{ movie.title }}
          </h4>
        </router-link>
      </swiper-slide>
    </swiper>
    <div
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <h3 class="text-white font-extrabold text-4xl text-left p-8">
        Режиссёр в фильмах
      </h3>
    </div>
    <swiper
      :modules="modules"
      :slides-per-view="4"
      navigation
      :pagination="{ clickable: true }"
      @slideChange="onSlideChange"
      class="container flex flex-wrap justify-evenly md:justify-between items-center mx-auto mt-12"
    >
      <swiper-slide
        v-for="movie in director.movies"
        :key="movie.id"
        class="transition hover:scale-110"
      >
        <router-link :to="'/' + movie.url" class="hover:text-cyan-50">
          <img class="rounded-xl mt-6 px-2" :src="movie.post" />
          <h4
            class="text-white text-center px-2 md:mb-8 pt-2 hover:text-cyan-500"
          >
            {{ movie.title }}
          </h4>
        </router-link>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import { API_URL } from "@/config";
import axios from "axios";
import { getZodiac, getAge, declOfNum, parseDate, formatDate } from "../utils";
export default {
  name: "DirectorDetail",
  props: ["id"],
  data() {
    return {
      director: {
        id: 0,
        name: "",
        description: "",
        image: "",
        place_of_birth: "",
        world_premiere: "",
      },
    };
  },
  created() {
    this.loadDirector();
  },
  methods: {
    async loadDirector() {
      const director = await fetch(
        `${API_URL}/v1/directors/${this.$route.params.id}`
      ).then((response) => response.json());
      director.date_of_birth = parseDate(director.date_of_birth);
      director.date_of_birth_str = formatDate(director.date_of_birth);
      director.zodiac = getZodiac(director.date_of_birth);
      director.age = getAge(director.date_of_birth);
      director.age_text = declOfNum(director.age, ["год", "года", "лет"]);
      this.director = director;
      console.log(this.director);
      console.log(this.id);
      console.log(director.date_of_birth);
      document.title = director.name;
    },
  },
  computed: {
    moviePoster() {
      return this.director.id == 0
        ? ""
        : "http://127.0.0.1:8000" + this.director.image;
    },
  },
};
</script>
