<template>
  <div
    class="flex flex-wrap flex-col md:flex-nowrap h-full justify-center items-center mt-16"
  >
    <div class="">
      <h3 class="text-center px-8 text-white text-2xl font-bold">
        Наслаждайся просмотром
      </h3>
      <p class="text-center px-8 text-gray-500 pt-4 text-xl font-semibold">
        Добро пожаловать на сайт Dokamosu, здесь вы можете расслабиться и хорошо
        провести время, так что запасайтесь вкусняшками и вперёд смотреть.
      </p>
    </div>
    <div>
      <img
        class="w-3/4 h-3/4 mx-auto"
        src="../assets/undraw_home_cinema_l7yl.svg"
        alt=""
      />
    </div>
  </div>
  <div
    class="home w-full grid grid-cols-2 md:flex flex-wrap justify-center mx-auto gap-5 md:gap-8 lg:gap-5"
  >
    <div v-for="movie in listMovie" :key="movie.id">
      <div
        class="transition duration-300 hover:scale-110 mx-auto h-2/4 w-2/4 md:w-3/4"
      >
        <router-link :to="'/' + movie.url" class="text-white font-bold">
          <img
            v-lazy:src="movie.post"
            class="rounded-xl hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
            alt="Фильм"
          />
          <h4 class="text-center mb-8">
            {{ movie.title }}
          </h4>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { API_URL } from "@/config";

export default {
  name: "HomeView",
  data() {
    return {
      listMovie: [],
      loading: true,
      errored: false,
    };
  },
  mounted() {
    axios
      .get(`${API_URL}/v1/movie`)
      .then((response) => {
        this.listMovie = response.data;
      })
      .catch((error) => {
        console.log(error);
        this.errored = true;
      })
      .finally(() => (this.loading = false));
    console.log("good");
  },
};
</script>
