const signs = [
  { name: "козерог", m: 1, d: 20 },
  { name: "водолей", m: 2, d: 20 },
  { name: "рыбы", m: 3, d: 20 },
  { name: "овен", m: 4, d: 20 },
  { name: "телец", m: 5, d: 20 },
  { name: "близнецы", m: 6, d: 21 },
  { name: "рак", m: 7, d: 22 },
  { name: "лев", m: 8, d: 23 },
  { name: "дева", m: 9, d: 23 },
  { name: "весы", m: 10, d: 23 },
  { name: "скорпион", m: 11, d: 22 },
  { name: "стрелец", m: 12, d: 21 },
  { name: "козерог", m: 13, d: 20 },
];

export function getZodiac(date) {
  for (let i = 0; i < signs.length; i++) {
    if (signs[i].m == date.getMonth() + 1 && signs[i].d >= date.getDate())
      return signs[i].name;
  }
}

export function getAge(birthDate) {
  var today = new Date();
  var age = today.getFullYear() - birthDate.getFullYear();
  var m = today.getMonth() - birthDate.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }
  return age;
}

export function declOfNum(number, titles) {
  let cases = [2, 0, 1, 1, 1, 2];
  return titles[
    number % 100 > 4 && number % 100 < 20
      ? 2
      : cases[number % 10 < 5 ? number % 10 : 5]
  ];
}

export function parseDate(dateStr) {
  const dateParts = dateStr.split("-");
  return new Date(dateParts[0], dateParts[1] - 1, dateParts[2]);
}

export function formatDate(date) {
  const months = [
    "января",
    "февраля",
    "марта",
    "апреля",
    "мая",
    "июня",
    "июля",
    "августа",
    "сентября",
    "октября",
    "ноября",
    "декабря",
  ];
  return `${date.getDate()} ${months[date.getMonth()]} ${date.getFullYear()}`;
}

export function isMobile() {
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
    navigator.userAgent
  );
}
