<template>
  <widget-container-modal />
  <div>
    <div v-if="movie" class="Detail"></div>
    <div
      class="absolute top-0 hidden md:block"
      style="z-index: -1; width: 100%"
    >
      <div
        class="w-full h-full from-black via-black to-black bg-gradient-to-t opacity-90 absolute"
      ></div>
      <img
        class="h-full w-full object-cover object-top"
        :src="movie.background"
      />
    </div>
    <div class="flex flex-wrap md:flex-wrap justify-evenly mt-32">
      <div>
        <img
          :src="moviePoster"
          class="rounded-xl block object-cover overflow-hidden"
          alt=""
        />
      </div>
      <div>
        <ul class="px-8 md:px-0">
          <h3
            class="font-extrabold text-4xl text-white text-center sm:text-left pt-4"
          >
            {{ movie.title }} ({{ movie.year }})
          </h3>
          <li class="pt-8">
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Год производства:</b>
              {{ movie.year }}</span
            >
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Страна:</b>
              {{ movie.coutry }}</span
            >
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Слоган:</b>
              {{ movie.tagline }}</span
            >
          </li>
          <li></li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Жанр: </b>
              <span v-for="(genre, index) in movie.genres" :key="genre"
                >{{ genre
                }}<span v-if="index + 1 < movie.genres.length">, </span>
              </span>
            </span>
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Премьера в мире:</b>
              {{ movie.world_premiere_str }}</span
            >
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Бюджет:</b> ${{
                movie.budget.toLocaleString()
              }}
            </span>
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Сборы в США:</b> ${{
                movie.fees_in_usa.toLocaleString()
              }}</span
            >
          </li>
          <li>
            <span class="text-white"
              ><b class="font-bold text-neutral-500">Сборы в мире:</b> ${{
                movie.fees_in_world.toLocaleString()
              }}</span
            >
          </li>
          <li>
            <div
              class="container w-2/4 flex flex-wrap flex-col justify-evenly md:justify-between items-center md:items-start mx-auto mt-12 transition duration-300 hover:scale-110"
            >
              <div>
                <router-link
                  :to="'/directors/' + movie.director.id"
                  class="hover:text-blue-50"
                >
                  <h3 class="text-white font-extrabold text-xl text-center">
                    Режиссёр
                  </h3>
                  <img
                    class="rounded-xl mt-6 hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
                    :src="movie.director.image_small"
                  />
                  <h4
                    class="text-white font-semibold text-center px-2 md:mb-8 pt-2 hover:text-blue-500"
                  >
                    {{ movie.director.name }}
                  </h4>
                </router-link>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="">
      <div class="flex content-center flex-wrap justify-evenly mx-auto">
        <swiper
          :modules="modules"
          :slides-per-view="
            movie.actors.length < slidesPerView
              ? movie.actors.length
              : slidesPerView
          "
          :navigation="!isMobile"
          class="mx-auto max-w-7xl"
        >
          <swiper-slide
            v-for="actor in movie.actors"
            :key="actor.id"
            class="transition duration-300 hover:scale-110"
          >
            <router-link :to="'/actors/' + actor.id" class="hover:text-cyan-50">
              <img
                class="rounded-xl mt-6 px-2 md:px-0 mx-auto hover:border-blue-500 hover:shadow-xl hover:shadow-blue-500/50"
                v-lazy:src="actor.image_small"
              />
              <h4
                class="text-white font-semibold text-center px-2 md:mb-8 pt-2 hover:text-blue-500"
              >
                {{ actor.name }}
              </h4>
            </router-link>
          </swiper-slide>
        </swiper>
      </div>

      <div class="mt-12 px-8 -z-50">
        <h3 class="font-extrabold text-4xl text-white text-center p-8">
          О фильме
        </h3>
        <p class="text-white text-xl pb-12">
          {{ movie.description }}
        </p>
        <h3 class="text-white text-center text-xl pt-12">
          Кадры из фильма {{ movie.title }}
        </h3>
        <div class="grid grid-cols-2 md:grid-cols-3 gap-8 mt-12">
          <span v-for="shot in movie.shots" :key="shot.id"
            ><a @click.prevent="showImage(shot.image)" href="#" target="_blank">
              <img
                :src="shot.image"
                class="rounded-xl p-2 transition duration-300 hover:z-50"
                alt="{{ shot.description }}"
            /></a>
          </span>
        </div>
      </div>
      <h3 class="font-extrabold text-4xl text-white text-center p-2 mt-8">
        Трейлер фильма
      </h3>
      <div class="flex flex-wrap md:flex-wrap justify-evenly items-center mt-8">
        <iframe
          width="854"
          height="480"
          :src="movie.movie_trailer"
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        >
        </iframe>
      </div>
      <div class="flex flex-wrap md:flex-wrap justify-evenly items-center mt-8">
        <iframe
          width="854"
          height="480"
          :src="movie.films"
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        >
        </iframe>
      </div>
    </div>
  </div>
</template>
<script>
import { computed } from "@vue/runtime-core";
import { formatDate, parseDate, isMobile } from "../utils";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation, Pagination, A11y, Grid, Autoplay } from "swiper";
import { openModal, container } from "jenesius-vue-modal";
import Modal from "./Modal.vue";
import { API_URL } from "@/config";

import "swiper/css";
import "swiper/css/grid";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/autoplay";

export default {
  components: {
    Swiper,
    SwiperSlide,
    WidgetContainerModal: container,
  },
  name: "Detail",
  props: ["id"],
  data() {
    return {
      options: {},
      movie: {
        id: 0,
        category: "фильмы",
        director: { id: 0 },
        actors: [],
        genres: [""],
        title: "",
        tagline: "",
        description: "",
        post: "",
        year: 0,
        coutry: "",
        world_premiere: "",
        budget: 0,
        fees_in_usa: 0,
        fees_in_world: 0,
        url: "",
        movie_trailer: "",
      },
    };
  },
  created() {
    this.loadMovie();
  },
  methods: {
    async loadMovie() {
      const movie = await fetch(
        `${API_URL}/v1/movie/${this.$route.params.id}`
      ).then((response) => response.json());
      movie.world_premiere = parseDate(movie.world_premiere);
      movie.world_premiere_str = formatDate(movie.world_premiere);
      this.movie = movie;
      console.log(this.movie);
      console.log(this.id);
      document.title = movie.title;
      const swiper = document.querySelector(".swiper").swiper;
      swiper.autoplay.start();
    },
    showImage(url) {
      openModal(Modal, {
        image: url,
      });
    },
  },
  computed: {
    moviePoster() {
      return this.movie.id == 0 ? "" : this.movie.post;
    },
    isMobile() {
      return isMobile();
    },
    slidesPerView() {
      return isMobile() ? 3 : 4;
    },
  },
  setup() {
    return {
      modules: [Navigation, Grid, Pagination, A11y, Autoplay],
    };
  },
};
</script>
<style></style>
